import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import { Row, Card, Button, Table, Col, Form, Input, Select, Spin, message } from 'antd';
import BtnAddNew from 'components/common/button/BtnAddNew';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import DatePickerString from 'components/common/input/DatePickerString';
import {
  getCodeStudent,
  getBonusType,
  getBonusTypeChild,
  addItemAddReward,
  getAddRewardById,
} from 'services/AddReward';

const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    birthdate: '',
    className: '',
    regNum: '',
    decDay: '',
    rewardLevel: '',
    form: '',
    typeOfReward: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormAddReward extends Component {
  state = {
    teacherId: false,
    initialValue: {},
    dataAddReward: [],
    isPending: false,
    bonusType: [],
    bonusTypeChild: {},
    bonusTypeChildNew: [],
  };

  componentDidMount = () => {
    const { match } = this.props;
    getBonusType()
      .then(res => {
        this.setState({
          bonusType: res.data.items,
        });
      })
      .catch(error => {
        console.log(error);
      });
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getAddRewardById(match.params.id);
    }
  };

  getAddRewardById = async id => {
    const { form } = this.props;
    const dataMap = await getAddRewardById(id);
    const initialValue = handleInitialValue(dataMap.data);
    dataMap.data.students.forEach(student => {
      this.chooseBonusType(student.parentBonusTypeId);
    });
    form.setFieldsValue(initialValue);
    this.setState({ initialValue, dataAddReward: dataMap.data.students, dataGetId: dataMap.data });
    console.log('initialValue', initialValue);
  };

  handleSubmit = e => {
    e.preventDefault();
    const { dataAddReward, teacherId, dataGetId } = this.state;
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if(!dataAddReward.length){
          message.warning('Phải có sinh viên được chọn!');
          return;
        };
        const data = [];
        console.log('values', values);
        if (teacherId) {
          const student = [];
          // eslint-disable-next-line array-callback-return
          dataAddReward.map(item => {
            const dataItem = {
              studentId: item.id,
              classId: item.classId,
              bonusTypeId: item.bonusTypeId,
            };
            student.push(dataItem);
          });
          const dataAdd = {
            approvalDate: values.approvalDate,
            approvalNumber: values.approvalNumber,
            description: values.description,
            id: dataGetId.id,
            semesterId: values.semesterId,
            yearEduId: values.yearEduId,
            students: student,
          };

          addItemAddReward(dataAdd)
            .then(() => {
              // console.log('res', res);
              // message.success('');
              this.props.history.goBack();
            })
            .catch(error => console.log('error', error));
        } else {
          dataAddReward.forEach(item => {
            const { id, classId, bonusTypeId } = item;
            const dataAddnew = {
              studentId: id,
              classId,
              bonusTypeId,
            };

            data.push(dataAddnew);
          });

          const addRewardNew = {
            ...values,
            students: data,
          };
          addItemAddReward(addRewardNew)
            .then(() => {
              this.props.history.goBack();
            })
            .catch(error => {
              console.log('error', error);
            });
        }
      }
    });
  };

  addDataAddReward = () => {
    const { dataAddReward } = this.state;
    const item = {
      id: '',
      classId: '',
      bonusTypeId: '',
    };
    dataAddReward.push(item);
    this.setState({
      dataAddReward,
    });
  };

  deleteData = index => {
    const { dataAddReward } = this.state;
    dataAddReward.splice(index, 1);
    this.setState({
      dataAddReward,
    });
  };

  changeInputCode = (e, record, number) => {
    const { dataAddReward } = this.state;
    this.setState({
      isPending: true,
    });
    getCodeStudent(e.target.value)
      .then(res => {
        // eslint-disable-next-line array-callback-return
        dataAddReward.map((item, index) => {
          if (index === number) {
            item.id = res.data.id;
            item.fullname = res.data.firstname + res.data.lastname;
            item.dob = res.data.dob;
            item.className = res.data.className;
            item.classId = res.data.classId;
          }
        });
        this.setState({
          dataAddReward,
          isPending: false,
        });
      })
      .catch(error => {
        console.log('error', error);
        this.setState({
          isPending: false,
        });
      });
  };

  changeBonusTypeChild = (e, number) => {
    const { dataAddReward } = this.state;
    // eslint-disable-next-line array-callback-return
    dataAddReward.map((item, index) => {
      if (index === number) {
        item.bonusTypeId = e;
      }
    });
    this.setState({
      dataAddReward,
    });
  };

  chooseBonusType = (value, record = '') => {
    const { dataAddReward } = this.state;
    this.setState({
      isPending: true,
    });
    getBonusTypeChild(value)
      .then(res => {
        this.setState({
          bonusTypeChild: {
            [value]: res.data.items,
          },

          bonusTypeChildNew: res.data.items,
        });
        this.setState({
          isPending: false,
        });
        if (record) {
          // eslint-disable-next-line array-callback-return
          dataAddReward.forEach(student => {
            if (student.id === record.id) {
              student.bonusTypeId = '';
            }
          });
          this.setState({
            dataAddReward,
          });
        }
      })
      .catch(error => {
        console.log(error);
        this.setState({
          isPending: false,
        });
      });
  };

  render() {
    const {
      initialValue,
      dataAddReward,
      isPending,
      bonusType,
      teacherId,
      bonusTypeChild,
      bonusTypeChildNew,
    } = this.state;
    const { getFieldDecorator } = this.props.form;
    const columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
        // eslint-disable-next-line no-unused-vars
        render: (text, record, index) => {
          const { code } = record;
          if (teacherId && code) {
            return <Input value={code} onBlur={e => this.changeInputCode(e, record, index)} />;
          }
          return <Input onBlur={e => this.changeInputCode(e, record, index)} />;
        },
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Lớp',
        dataIndex: 'className',
        key: 'className',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Cấp khen thưởng',
        render: (text, record) => (
          <Select
            style={{ width: '100%' }}
            defaultValue={record.parentBonusTypeId}
            onChange={e => this.chooseBonusType(e, record)}
          >
            {bonusType.map(item => (
              <Option key={item.id} value={item.id}>
                {item.label}
              </Option>
            ))}
          </Select>
        ),
      },
      {
        title: 'Loại khen thưởng',
        render: (text, record, index) => (
          <Select
            style={{ width: '100%' }}
            value={record.bonusTypeId}
            onChange={e => this.changeBonusTypeChild(e, index)}
          >
            {record.bonusTypeId && record.approvalId
              ? (bonusTypeChild[record.parentBonusTypeId] || []).map(item => (
                  // eslint-disable-next-line react/jsx-indent
                  <Option key={item.id} value={item.id}>
                    {item.label}
                  </Option>
                ))
              : bonusTypeChildNew.map(item => (
                  // eslint-disable-next-line react/jsx-indent
                  <Option key={item.id} value={item.id}>
                    {item.label}
                  </Option>
                ))}
          </Select>
        ),
      },
      {
        title: 'Thao tác',
        dataIndex: 'rewardType',
        render: (text, record, index) => (
          <Button
            className="btn-delete"
            icon="delete"
            size="small"
            onClick={() => this.deleteData(index)}
          />
        ),
      },
    ];
    const optionFieldDecorator = {
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Card title={teacherId ? 'Cập nhật khen thưởng' : 'Thêm khen thưởng'}>
        <Row>
          <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSubmit}>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Số QĐ" hasFeedback>
                {getFieldDecorator('approvalNumber', {
                  initialValue: initialValue.approvalNumber,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Ngày QD" hasFeedback>
                {getFieldDecorator('approvalDate', {
                  initialValue: initialValue.approvalDate,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<DatePickerString placeholder="Chọn ngày" />)}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Hình thức khen thưởng" hasFeedback>
                {getFieldDecorator('description', {
                  initialValue: initialValue.description,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
            <Col xs={24} sm={12} md={6}>
              <Form.Item label=" ">
                <BtnSave htmlType="submit" />
              </Form.Item>
            </Col>
          </Form>
        </Row>
        <Row>
          <BtnAddNew onClick={this.addDataAddReward} />
        </Row>
        <Row>
          <Col span={24}>
            <Spin spinning={isPending}>
              <Table onRow={this.rowTable} columns={columns} dataSource={dataAddReward} />
            </Spin>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddReward {...props} />
  </Page>
));



